import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import logo from './Marathon.png';

export const Navbar = () => {
  const [fullName, setFullName] = useState("");
  const navigate = useNavigate();

  const onSubmit = (e) => {
    e.preventDefault();
    if (fullName.trim()) {
      navigate(`/viewuser/${fullName}`); // Updated navigation path
    }
  };

  return (
    <div>
      <nav className="navbar navbar-light" style={{ backgroundColor: '#2E5077' }}>
        <div className="container-fluid"/>
          <div className="navbar-brand d-flex align-items-center">
            <img
              src={logo} // Replace with the correct logo path
              alt="Marathon Logo"
              width="55"
              height="55"
              className=" mx-2" 
            />
            <span style={{ color: "white" }}>Marathon 15th Edition du Maroc</span>
          </div>
        <form className="d-flex" onSubmit={onSubmit}>
          <input
            type="text"
            className="form-control me-2"
            placeholder="Search"
            aria-label="Search"
            value={fullName}
            onChange={(e) => setFullName(e.target.value)}
          />
          <button className="btn " type="submit" style={{ backgroundColor: "#4DA1A9", color: "white" }}>
            Search
          </button>
          <Link className="btn mx-2" to="/AllUsers" style={{ backgroundColor: "#4DA1A9", color: "white" }}>Results</Link>
          <Link className="btn  mx-2" to="/VisualisationEvent" style={{ backgroundColor: "#4DA1A9", color: "white" }}>Visualizations</Link>
        </form>
      </nav>
    </div>
  );
};
