import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";

export const TableByName = () => {
  const { fullName } = useParams(); // Get dynamic parameter
  const [participant, setParticipant] = useState(null);

  useEffect(() => {
    loadParticipant();
  }, [fullName]); // Add fullName as a dependency to re-run when it changes

  const loadParticipant = async () => {
    try {
      const result = await axios.get(
        `http://localhost:8080/marathons/participants/${fullName}`
      );
      setParticipant(result.data);
    } catch (error) {
      console.error("Error fetching participant:", error);
    }
  };

  return (
    <div>
      {participant ? (
        <table className="table mt-2" style={{ backgroundColor: '#81BFDA' }}>
          <thead>
            <tr style={{ backgroundColor: '#81BFDA' }}>
              <th scope="col">Position</th>
              <th scope="col">Full Name</th>
              <th scope="col">Age</th>
              <th scope="col">Net Time</th>
              <th scope="col">Gender</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th scope="row">{participant.position}</th>
              <td>{participant.fullName}</td>
              <td>{participant.age}</td>
              <td>{participant.netTime}</td>
              <td>{participant.gender}</td>
            </tr>
          </tbody>
        </table>
      ) : (
        <p>Loading participant data...</p>
      )}
    </div>
  );
};
