import React, { useEffect, useState } from "react";
import axios from "axios";

export const Table = () => {
  const [participants, setParticipants] = useState([]);

  useEffect(() => {
    loadParticipants();
  }, []);

  const loadParticipants = async () => {
    try {
      const result = await axios.get(
        "http://localhost:8080/marathons/participants/all"
      );
      setParticipants(result.data);
    } catch (error) {
      console.error("Error fetching participants:", error);
    }
  };

  return (
    <div>
      {/* Inline style applied directly to the table */}
      <table
        className="table mt-2"
        style={{ backgroundColor: "#81BFDA", color: "black" }} // Table background and text color
      >
        {/* Inline style for the table header */}
        <thead style={{ backgroundColor: "#5A9DB6", color: "white" }}> 
          <tr>
            <th scope="col">Position</th>
            <th scope="col">Full Name</th>
            <th scope="col">Age</th>
            <th scope="col">Net Time</th>
            <th scope="col">Gender</th>
          </tr>
        </thead>
        {/* Inline style for the table rows */}
        <tbody>
          {participants.map((participant) => (
            <tr
              key={participant.position}
              style={{ backgroundColor: "#E8F4F8" }} // Row background color
            >
              <th scope="row">{participant.position}</th>
              <td>{participant.fullName}</td>
              <td>{participant.age}</td>
              <td>{participant.netTime}</td>
              <td>{participant.gender}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};
