import React, { useEffect, useState } from 'react';
import { Bar, Doughnut, Scatter } from 'react-chartjs-2';
import axios from 'axios';
import { Chart as ChartJS, defaults } from "chart.js/auto";
import "../Visualisation.css"

defaults.maintainAspectRatio = false;
defaults.responsive = true;

defaults.plugins.title.display = true;
defaults.plugins.title.align = "start";
defaults.plugins.title.font.size = 20;
defaults.plugins.title.color = "black";




export const Visualisation = () => {


    // FOR AgeDistribution

    const [labels, setLabels] = useState([]);
    const [counts, setCounts] = useState([]);
    const [averages, setAverages] = useState([]);


    useEffect(() => {
        loadChartData();
    }, []);


    const loadChartData = async () => {
        try {
            const response = await axios.get('http://localhost:8080/marathons/participants/ageDistribution');
            const fetchedLabels = Object.keys(response.data);
            const fetchedCounts = fetchedLabels.map(label => response.data[label].count);
            const fetchedaverages = fetchedLabels.map(label => response.data[label].averagePerformance)
            console.log(fetchedCounts)

            setLabels(fetchedLabels);
            setCounts(fetchedCounts);
            setAverages(fetchedaverages);

        } catch (error) {
            console.error('Error fetching chart data:', error);
        }
    };

    // FOR GENDER DISTRIBUTION

    const [labels1, setLabels1] = useState([]);
  const [data1, setData1] = useState([]);

  useEffect(() => {
    loadChartData1();
  }, []);

  const loadChartData1 = async () => {
    try {
      const response = await axios.get(
        "http://localhost:8080/marathons/participants/genderDistribution"
      );
      const fetchedLabels = Object.keys(response.data);
      const fetchedData = Object.values(response.data);

      setLabels1(fetchedLabels);
      setData1(fetchedData);

    } catch (error) {
      console.error("Error fetching chart data:", error);
    }
  };

  // FOR SCATTER PLOT 

  const [ages, setAges] = useState([]);
  const [data2, setData2] = useState([]);
  const [netTime, setNetTime] = useState([]);
  useEffect(() => {
    loadChartData2();
  }, []);

  const loadChartData2 = async () => {
    try {
      const response = await axios.get(
        "http://localhost:8080/marathons/participants/scatterAgeNet"
      );
      const fetchedAges = Object.keys(response.data);
      const fetchedNetTime = Object.values(response.data);
      setAges(fetchedAges);
      setNetTime(fetchedNetTime);
      const data = fetchedNetTime[0].map((age, index) => ({
        x: fetchedNetTime[1][index],
        y: age,
      }));
      setData2(data);
    } catch (error) {
      console.error("Error fetching chart data:", error);
    }
  };





  return (
    <div className="AppVisualisation">
    <div className="dataCard revenueCard">

    <Scatter
                data={{
                  datasets: [
                    {
                      label: "Scatter Resultat",
                      data: data2,
                      backgroundColor: "rgba(52, 143, 204, 0.7)",
                    },
                  ],
                }}
                options={{
                    plugins: {
                      title: {
                        text: "Scatter plot Age vs Net Time",
                      },
                    },
                  }}
              />


    </div>

    <div className="dataCard customerCard">

        <Doughnut
                data={{
                  labels: labels1,
                  datasets: [
                    {
                      label: "Number of people participating",
                      data: data1,
                      backgroundColor: [
                        "rgba(255, 99, 132, 0.7)",
                        "rgba(52, 143, 204, 0.7)",
                      ],
                    },
                  ],
                }}
                options={{
                    plugins: {
                      title: {
                        text: "Gender Distribution",
                      },
                    },
                  }}
              />


    </div>

    <div className="dataCard categoryCard">


<Bar 
                    data = {{ 
                        labels : labels,
                        datasets : [
        
                            {
                                label : "Number of people participating",
                                data  : counts,
                                backgroundColor: [
                                    "rgba(52, 143, 204, 0.7)",
                                  ],
                            },
                            {
                                label : "Average Performance (seconds)",
                                data : averages,
                                backgroundColor: "rgba(255, 99, 132, 0.7)",
                            }
                        ],   
                    }}
                    options={{
                        elements: {
                          line: {
                            tension: 0.5,
                          },
                        },
                        plugins: {
                          title: {
                            text: "Age Distribution by Number and Performance",
                          },
                        },
                      }}
            
            />

              


    </div>
  </div>
  )
}
